<script>
  import Footer from "../components/Footer.vue";

  export default {
    name: 'Home',
    title: "Nethanzêl",
    components: {
      Footer
    },
    mounted() {
      this.imageExchange();
    },
    methods: {
      imageExchange() {
        let container = this.$refs.exTarget;
        let imgCollection = container.getElementsByTagName("img");
        
        let index = 0;
        let beforeIndex = 0;

        for(let image of imgCollection) {
          if(index == 0) {
            index = 1; 
            continue;
          }
          image.style.opacity = 0;
        }

        setInterval(() => {
          
          imgCollection[index].style.opacity = 1;

          if(beforeIndex != undefined) imgCollection[beforeIndex].style.opacity = 0;

          beforeIndex = index;
          index = index+1;

          if(index > imgCollection.length -1) index = 0;
        }, 5000);

      },
      showCVOptions() {
        this.$refs.cvDownload.style.display = "none";
        this.$refs.cvOptions.style.display = "flex";
        setTimeout(() => {
          this.$refs.cvDownload.style.display = "flex";
          this.$refs.cvOptions.style.display = "none";
        }, 15000);
      },
      openLink_(url, target) {
        console.log(target);
        window.open(url, target);
      }
    }
  }
</script>

